import axios from "axios";

let baseURL = "http://localhost:3000";
if (process.env.NODE_ENV === "production") {
  baseURL = "https://recetario-core.herokuapp.com";
}

const api = axios.create({ baseURL });

export interface RecetaCreate {
  name: string;
  description: string;
}

export interface Receta extends RecetaCreate {
  _id: string;
}

export const createReceta = async (data: RecetaCreate) => {
  const res = await api.post<Receta>("/", data);
  console.log("DATA CREATED IN RECETAS API");
  return res.data;
};

export const getAllRecetas = async () => {
  const res = await api.get<Receta[]>("/");
  return res.data;
};

export const deleteReceta = async (delete_id: string) => {
  await api.delete(`/${delete_id}`);
  console.log("RECETA DELETED");
  return;
};
