import React from "react";
import { useForm } from "react-hook-form";

import { Input } from "./widgets/Input";
import cx from "classnames";
import { useRecipes, RecetaCreate } from "../../lib/use-recipes";

export const RecetaForm = () => {
  const { handleSubmit, reset, control, formState } = useForm<RecetaCreate>();
  const [_, { create_receta_api }] = useRecipes();

  const submit = handleSubmit(async (data: RecetaCreate) => {
    await create_receta_api(data);
    reset({ name: "", description: "" });
  });

  return (
    <div>
      <Input
        name="name"
        label="Nombre de la receta"
        control={control}
        rules={{ required: "ponle nombre a la receta" }}
      />
      <Input
        name="description"
        label="Descripción de la receta"
        control={control}
        // rules={{ required: "describe la receta" }}
      />
      <Input
        name="price"
        label="Precio de la receta"
        type="number"
        control={control}
        // rules={{ required: "describe la receta" }}
      />
      <button
        className={cx("btn btn-primary", { disabled: formState.isSubmitting })}
        onClick={submit}
      >
        {formState.isSubmitting && (
          <span
            class="spinner-border spinner-border-sm"
            role="status"
            aria-hidden="true"
          />
        )}
        Crear receta
      </button>
    </div>
  );
};
