import React, { useEffect } from "react";
import { RecetaForm } from "./forms/RecetaForm";
import { Columns } from "./templates/Columns";
import { RecetasList } from "./recetas/RecetasList";
import { AlbertoContador } from "./Contador";
import { RecipesContainer, useRecipes } from "../lib/use-recipes";

const InternalApp = () => {
  const [_, { refresh_recetas }] = useRecipes();
  // Initially load all the recipes
  useEffect(() => {
    refresh_recetas();
  }, []);
  return (
    <div className="container">
      <h2>Recetario</h2>
      <AlbertoContador />
      <Columns num_cols={2}>
        <RecetasList />
        <RecetaForm />
      </Columns>
    </div>
  );
};

export const App = () => {
  return <InternalApp />;
};
