import React from "react";
import { Receta as RecetaType } from "../../lib/api";
import styled from "styled-components";
import { useRecipes } from "../../lib/use-recipes";
import { useSelectRecipes } from "../../lib/use-select-recipes";

const Wrap = styled.div`
  border: 1px solid blue;
  margin: 5px;
`;

export const Receta: React.FC<{
  receta: RecetaType;
}> = ({ receta }) => {
  const [_, { delete_receta }] = useRecipes();
  const [isSelected, { do_select }] = useSelectRecipes({ id: receta._id });

  return (
    <Wrap>
      <span onClick={() => do_select(receta._id, true)}>
        Selected: {isSelected ? "✅" : "❌"}
      </span>
      <h3>
        RECETA: {receta.name} (id:{receta._id})
      </h3>
      <p>{receta.description}</p>
      <button onClick={() => delete_receta(receta._id)}>❌ Delete this</button>
    </Wrap>
  );
};
