import React from "react";
import styled from "styled-components";
import { useCounter } from "../lib/use-counter";

const Wrapper = styled.div`
  padding: 10px;
  margin: 10px 0;
  border: 1px solid green;
  .count {
    color: red;
  }
`;

export const AlbertoContador = () => {
  const [{ count }, actions] = useCounter();

  return (
    <Wrapper>
      <div>
        La cuenta está en: <span className="count">{count}</span>
      </div>
      <button onClick={() => actions.increment(1)}>Más</button>
      <button onClick={() => actions.increment(-1)}>Menos</button>
    </Wrapper>
  );
};
