import React from "react";
import styled from "styled-components";
import { v4 as uuidv4 } from "uuid";

const ColumnsWrapper = styled.div`
  display: grid;
  grid-template-columns: ${({ num_cols }) => "1fr ".repeat(num_cols)};
  grid-gap: 8px;
  .col {
    border: 1px solid red;
  }
`;

type ColumnsProps = {
  children: React.Node[];
  num_cols: number;
};

export const Columns: React.FC<ColumnsProps> = ({ children, num_cols = 2 }) => {
  return (
    <ColumnsWrapper num_cols={num_cols}>
      {children.map((col) => (
        <div className="col" key={uuidv4()}>
          {col}
        </div>
      ))}
    </ColumnsWrapper>
  );
};
