import {
  createStore,
  createHook,
  defaults,
  Action,
  createContainer,
} from "react-sweet-state";
import { Receta } from "../redux/actions";
import { createReceta, deleteReceta, getAllRecetas, RecetaCreate } from "./api";
defaults.devtools = true;

export { RecetaCreate };

type State = {
  recipes: Receta[];
};

const initialState: State = {
  recipes: [],
};

// Guarda las recetas en el estado local de react-sweet-state
const create_recipe = (recipe: Receta): Action<State> => ({
  setState,
  getState,
}) => {
  // mutate state syncronously
  setState({ recipes: [...getState().recipes, recipe] });
};

const create_receta_api = (data: RecetaCreate): Action<State> => async ({
  dispatch,
}) => {
  const created_receta = await createReceta(data);
  dispatch(create_recipe(created_receta));
};

// Borra todas las recetas del estado local
const delete_all = (): Action<State> => ({ setState }) => {
  setState({ recipes: [] });
};

// Borra solo una receta segun el id
const delete_receta = (id: string): Action<State> => async ({
  getState,
  setState,
}) => {
  const recetas = getState().recipes;
  await deleteReceta(id);
  setState({ recipes: recetas.filter((e) => e._id != id) });
};

// Pide las recetas de la api, borra las que hay y añade las que has recibido
const refresh_recetas = () => async ({ setState, getState, dispatch }) => {
  const recetas = await getAllRecetas();

  // NOTA: Low performance, tooo much re-renders
  //dispatch(delete_all());
  //recetas.forEach((r) => dispatch(create_recipe(r)));

  setState({ recipes: recetas });
};

const Store = createStore({
  // value of the store on initialisation
  initialState,
  // actions that trigger store mutation
  actions: {
    create_recipe,
    delete_all,
    refresh_recetas,
    delete_receta,
    create_receta_api,
  },
  // optional, mostly used for easy debugging
  name: "recipes",
});

export const useRecipes = createHook(Store);
export const RecipesContainer = createContainer(Store);
