import { createStore, createHook, Action } from "react-sweet-state";

type State = {
  selected: string[];
};

const initialState: State = {
  selected: [],
};

const do_select = (
  id: string,
  deselect_if_selected = false
): Action<State> => ({ setState, getState }) => {
  const selected = getState().selected;
  const found = selected.findIndex((e) => e == id);
  if (found == -1) {
    console.log("The item was not previously selected, selecting.");
    setState({ selected: [...selected, id] });
  } else {
    if (deselect_if_selected) {
      console.log("Item is already selected -> Deselecting");
      setState({ selected: selected.filter((e) => e != id) });
    } else {
      console.log("Item is already selected -> Doing nothing");
    }
  }
};

const deselect_all = (): Action<State> => ({ setState }) => {
  setState({ selected: [] });
};

const Store = createStore({
  // value of the store on initialisation
  initialState,
  // actions that trigger store mutation
  actions: { do_select, deselect_all },
  // optional, mostly used for easy debugging
  name: "selected",
});

// SELECTOR
const is_recipe_selected = ({ selected }, { id }) => {
  const found = selected.findIndex((e) => e == id);
  return found != -1;
};

export const useSelectRecipes = createHook(Store, {
  selector: is_recipe_selected,
});

const totalize_selector = ({ selected }) => selected.length;

export const useSelectRecipesTotalize = createHook(Store, {
  selector: totalize_selector,
});

export const useSelectRecipesNormal = createHook(Store);
