import React from "react";
import { useRecipes } from "../../lib/use-recipes";
import {
  useSelectRecipesTotalize,
  useSelectRecipesNormal,
} from "../../lib/use-select-recipes";
import { Receta } from "./Receta";

export const RecetasList = () => {
  const [{ recipes }, { refresh_recetas, delete_receta }] = useRecipes();
  console.log("Inside list", recipes);
  if (recipes.length == 0)
    return <div> CREA UNA RECETA USANDO EL FORMULARIO DE LA DERECHA</div>;

  const [total, { deselect_all }] = useSelectRecipesTotalize();
  const [{ selected }] = useSelectRecipesNormal();
  return (
    <div>
      <button onClick={() => refresh_recetas()}>Refresh recetas</button>
      {total > 0 && (
        <>
          <button onClick={() => deselect_all()}>
            Deselect all ({total}) recetas
          </button>
          <button
            onClick={async () => {
              await Promise.all(selected.map(delete_receta));
              deselect_all();
            }}
          >
            Delete all selected ({total}) recetas
          </button>
        </>
      )}
      {recipes.map((receta) => (
        <Receta key={receta._id} receta={receta} />
      ))}
    </div>
  );
};
